@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700&display=swap");
@import "./components/catalog.css";
* {
 padding: 0;
 margin: 0;
}
.active {
 font-weight: 700;
}
body {
 background-image: url(./img/bg_header-img.png);
 background-repeat: no-repeat;
 background-color: rgba(24, 23, 21, 1);
 color: rgba(255, 255, 255, 1);
 font-family: "Montserrat", sans-serif;
 background-size: 100vw;
}
.menu_img {
 position: absolute;
 opacity: 0;
 visibility: hidden;
 right: 3%;
 top: 5%;
}
.header_page {
 width: calc(100vw - 5.857vw);
 margin: 0 auto;
 margin-top: 30px;
 display: flex;
 align-items: center;
 justify-content: space-between;
 h1 {
  font-size: 34px;
 }
 ul {
  list-style: none;
  display: flex;
  gap: 4.171vw;
  li {
   font-size: 18px;
   cursor: pointer;
  }
 }
}
.vector-main {
 position: relative;
 top: 50px;
 width: 99vw;
 overflow: hidden;
}
main {
 width: calc(100vw - 5.857vw);
 margin: 0 auto;
 padding-top: 170px;
 position: relative;
 h2 {
  width: 38.753vw;
  font-size: 52px;
  line-height: 62px;
 }
 p {
  width: 29.283vw;
  font-size: 24px;
  margin-top: 30px;
 }
 button {
  margin-top: 62px;
  width: 18.302vw;
  height: 50px;
  border-radius: 50px;
  border-style: none;
  box-shadow: -1px -1px 10px rgba(255, 255, 255, 0.6),
   1px 1px 10px rgb(255, 255, 255, 0.6);
  font-size: 20px;
  color: black;
  cursor: pointer;
 }
}
.compani {
 background-color: rgba(24, 23, 21, 1);
 padding-top: 124px;
 .company-img {
  width: 49.048vw;
  height: auto;
 }
 h2 {
  width: 20.425vw;
  margin: 0 auto;
  font-size: 42px;
 }
 .section-container {
  width: calc(100vw - 5.857vw);
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  margin-top: 73px;
  p {
   font-size: 24px;
   width: 40.51vw;
   line-height: 30px;
  }
  ul {
   list-style: none;
  }
 }
 button {
  margin-top: 60px;
  width: 18.302vw;
  height: 50px;
  border-radius: 50px;
  border-style: none;
  box-shadow: -1px -1px 10px rgba(255, 255, 255, 0.6),
   1px 1px 10px rgb(255, 255, 255, 0.6);
  font-size: 20px;
  color: black;
  cursor: pointer;
 }
}
aside {
 position: relative;
 width: calc(100vw - 2.343vw);
 margin: 0 auto;
 margin-top: 170px;
 .img-left {
  position: absolute;
  top: 0;
  left: 0;
  width: 23vw;
 }
 .img-right {
  position: absolute;
  top: 0;
  right: 0;
  width: 23vw;
 }
 h2 {
  text-align: center;
  padding-top: 40px;
  font-size: 42px;
 }
 .partners {
  width: calc(100vw - 25.343vw);
  margin: 0 auto;
  text-align: center;
  .text-partners {
   margin-top: 100px;
   font-size: 30px;
   display: flex;
   justify-content: space-between;
  }
 }
 .description {
  width: calc(100vw - 45.343vw);
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  margin-top: 100px;
 }
}
.catalog {
 width: calc(100vw - 26.354vw);
 margin: 0 auto;
 margin-top: 200px;
 text-align: center;
 h2 {
  text-align: center;
  padding-top: 40px;
  font-size: 42px;
 }
 ul {
  list-style: none;
  display: flex;
  gap: 50px;
  margin-top: 100px;
  li {
   font-size: 24px;
   text-decoration: none;
   margin: auto;
   span {
    text-decoration: none;
   }
  }
 }
}
.connection {
 text-align: center;
 font-size: 24px;
 font-weight: 500;
 width: 30vw;
 button {
  margin-top: 60px;
  width: 18.302vw;
  height: 50px;
  border-radius: 50px;
  border-style: none;
  box-shadow: -1px -1px 10px rgba(255, 255, 255, 0.6),
   1px 1px 10px rgb(255, 255, 255, 0.6);
  font-size: 20px;
  color: black;
  cursor: pointer;
 }
}
.advantages {
 width: 99vw;
 background-image: url(./img/bg-.png);
 background-repeat: no-repeat;
 background-size: 100%;
 margin-top: 150px;
 position: relative;
 .vector {
  width: 99vw;
 }
 h3 {
  font-size: 42px;
  text-align: center;
  position: relative;
  bottom: 150px;
 }
 .advanteges_container {
  width: calc(100vw - 33vw);
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  gap: 5vw;
  .img_container {
   background-color: rgba(24, 23, 21, 0.6);
   border-radius: 25px;
   padding: 22px 76px;
   width: 21.449vw;
   img {
    width: 10.981vw;
   }
   p {
    text-align: center;
   }
  }
 }
}
.pos-bot {
 position: relative;
 top: 80px;
}
.pos-top {
 position: relative;
 bottom: 100px;
}

h3 {
 text-align: center;
 font-size: 42px;
 margin-bottom: 77px;
}
.contact {
 width: calc(100vw - 5.857vw);
 margin: 0 auto;
 display: flex;
 justify-content: space-between;

 ul {
  list-style: none;
  li {
   font-size: 24px;
  }
 }
}
.yandex {
 width: 54.1vw;
 height: 350px;
}

footer {
 margin-top: 200px;
 padding-top: 70px;
 padding-bottom: 120px;
 background-color: black;
 h1 {
  width: calc(100vw - 5vw);
  margin: 0 auto;
  font-size: 42px;
 }
}
.vector-bottom {
 position: relative;
 top: 15px;
}
@media (max-width: 1450px) {
 .advantages {
  background-size: 100% 100vw;
  .advanteges_container {
   gap: 0;
   position: relative;
   bottom: 100px;
   width: 100vw;
  }
 }
 .item_container {
  .corner_right,
  .corner_left {
   width: 12.299vw;
  }
  .item {
   height: 270px;
  }
  .mane_photo {
   height: 200px;
  }
 }
 .compani {
  .section-container {
   p {
    font-size: 20px;
   }
  }
  .company-img {
   height: 400px;
  }
  h2 {
   width: 50vw;
   text-align: center;
  }
 }
 main {
  padding-top: 50px;
  h2 {
   width: 40vw;
   font-size: 38px;
   line-height: 50px;
  }
  p {
   width: 40vw;
   font-size: 22px;
   margin-top: 10px;
  }
 }
 .vector-main {
  top: 125px;
 }
}
@media (max-width: 1150px) {
 .form_contact {
  width: 80vw !important;
  left: 10vw !important;
  ul {
   width: 26vw !important;
  }
  input,
  textarea,
  h2,
  .checkbox {
   width: 45vw !important;
  }
 }
 .advantages {
  .advanteges_container {
   gap: 0;
   position: relative;
   bottom: 100px;
   .img_container {
    width: 10vw;
   }
  }
 }
 .header_page {
  ul {
   li {
    font-size: 15px;
   }
  }
 }
 main {
  padding-top: 50px;
  h2 {
   width: 40vw;
   font-size: 32px;
   line-height: 35px;
   margin-bottom: 25px;
  }
  p {
   width: 40vw;
   font-size: 22px;
   margin-top: 0;
  }
 }
 .vector-main {
  top: 0px;
 }
}

@media (max-width: 1000px) {
 body {
  background-size: 100% 100vw;
  .vector-main {
   top: 10px;
  }
 }
 .menu_img {
  width: 10vw;
  opacity: 1;
  visibility: visible;
 }
 .header_page {
  nav {
   ul {
    position: absolute;
    background-color: black;
    width: 100vw;
    height: 200px;
    opacity: 0;
    display: block;
    right: 13%;
    line-height: 40px;
    z-index: 2;
    text-align: center;
    visibility: hidden;
   }
  }
 }
}
@media (max-width: 800px) {
 .vector-main {
  top: 10px;
 }
 .contact {
  ul {
   li {
    font-size: 18px;
   }
  }
 }
 main {
  button {
   font-size: 14px;
   margin-top: 15px;
  }
  h2 {
   font-size: 25px;
   width: 50vw;
  }

  p {
   font-size: 14px;
   width: 50vw;
  }
 }
 .compani {
  .company-img {
   height: 220px;
  }
  button {
   font-size: 14px;
   margin-top: 15px;
  }
  .section-container {
   p {
    font-size: 14px;
    line-height: 20px;
   }
  }
 }
 aside {
  img {
   width: 30vw;
  }
  .partners {
   .text-partners {
    font-size: 20px;
   }
  }
 }
 .catalog {
  ul {
   gap: 10px;
   li {
    font-size: 16px;
   }
  }
 }
 .item_container {
  grid-template-columns: repeat(2, 40vw);
  gap: 10px;
  .item {
   width: 40vw;
   .mane_photo {
    width: 37vw;
   }
  }
 }
 .connection {
  width: 100vw;
  h4 {
   width: 70vw;
   margin: 0 auto;
  }
 }
 .advantages {
  .img_container {
   top: 0px;
  }
  .advanteges_container {
   left: 0%;
   .img_container {
    width: 15vw;
    font-size: 15px;
    padding: 10px 40px;
   }
  }
 }
}

@media (max-width: 500px) {
 .item_container {
  grid-template-rows: repeat(6, 200px);
  .item {
   height: 150px;
   .mane_photo {
    height: 130px;
   }
  }
 }
 .advantages {
  .advanteges_container {
   display: block;
   position: relative;
   left: 0%;
   .img_container {
    left: 25%;
   }
  }
 }
 body {
  background-size: 100vw 130vw;
 }
 button {
  font-size: 14px !important;
  width: 50vw !important;
 }
 .catalog {
  ul {
   display: block;
  }
 }
}

.activeMenu {
 opacity: 1 !important;
 visibility: visible !important;
}

.form_contact {
 width: 50vw;
 height: 700px;
 position: fixed;
 background-color: rgb(37, 41, 41);
 z-index: 3;
 margin: 0 auto;
 left: 30vw;
 top: 15%;
 visibility: hidden;
 ul {
  background-image: url("./img/form_img.png");
  background-repeat: no-repeat;
  position: absolute;
  width: 15vw;
  height: 100%;
  padding-left: 10px;
  list-style: none;
 }
 display: flex;
 form {
  position: relative;
  left: 37%;
  z-index: 5;
  display: block;
  font-size: 34px;
  width: 27vw;
  text-align: center;
  button {
   width: 18.302vw;
   height: 50px;
   border-radius: 50px;
   border-style: none;
   box-shadow: -1px -1px 10px rgba(255, 255, 255, 0.6),
    1px 1px 10px rgb(255, 255, 255, 0.6);
   font-size: 20px;
   color: black;
  }
  h2 {
   margin-bottom: 30px;
  }
  input {
   width: 27vw;
   height: 50px;
   background-color: rgba(0, 0, 0, 0);
   border: 4px white solid;
   margin-bottom: 30px;
   color: white;
   font-size: 20px;
  }
  ::placeholder {
   font-size: 20px;
   color: white;
   padding-left: 15px;
   height: 100%;
  }
  textarea {
   color: white;
   width: 27vw;
   background-color: rgba(0, 0, 0, 0);
   border: 4px white solid;
  }
 }
 .checkbox {
  font-size: 16px;
  span {
   position: relative;
   bottom: 20px;
  }
  input {
   width: 1vw;
  }
 }
}

.activeForm {
 visibility: visible;
}

@media (max-width: 1700px) {
 .form_contact {
  form {
   h2 {
    font-size: 30px;
   }
  }
 }
}
