@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700&display=swap");
@import "./components/catalog.css";
* {
  padding: 0;
  margin: 0;
}

.active {
  font-weight: 700;
}

body {
  background-image: url(./img/bg_header-img.png);
  background-repeat: no-repeat;
  background-color: rgb(24, 23, 21);
  color: rgb(255, 255, 255);
  font-family: "Montserrat", sans-serif;
  background-size: 100vw;
}

.menu_img {
  position: absolute;
  opacity: 0;
  visibility: hidden;
  right: 3%;
  top: 5%;
}

.header_page {
  width: 94.143vw;
  margin: 0 auto;
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header_page h1 {
  font-size: 34px;
}
.header_page ul {
  list-style: none;
  display: flex;
  gap: 4.171vw;
}
.header_page ul li {
  font-size: 18px;
  cursor: pointer;
}

.vector-main {
  position: relative;
  top: 50px;
  width: 99vw;
  overflow: hidden;
}

main {
  width: 94.143vw;
  margin: 0 auto;
  padding-top: 170px;
  position: relative;
}
main h2 {
  width: 38.753vw;
  font-size: 52px;
  line-height: 62px;
}
main p {
  width: 29.283vw;
  font-size: 24px;
  margin-top: 30px;
}
main button {
  margin-top: 62px;
  width: 18.302vw;
  height: 50px;
  border-radius: 50px;
  border-style: none;
  box-shadow: -1px -1px 10px rgba(255, 255, 255, 0.6), 1px 1px 10px rgba(255, 255, 255, 0.6);
  font-size: 20px;
  color: black;
  cursor: pointer;
}

.compani {
  background-color: rgb(24, 23, 21);
  padding-top: 124px;
}
.compani .company-img {
  width: 49.048vw;
  height: auto;
}
.compani h2 {
  width: 20.425vw;
  margin: 0 auto;
  font-size: 42px;
}
.compani .section-container {
  width: 94.143vw;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  margin-top: 73px;
}
.compani .section-container p {
  font-size: 24px;
  width: 40.51vw;
  line-height: 30px;
}
.compani .section-container ul {
  list-style: none;
}
.compani button {
  margin-top: 60px;
  width: 18.302vw;
  height: 50px;
  border-radius: 50px;
  border-style: none;
  box-shadow: -1px -1px 10px rgba(255, 255, 255, 0.6), 1px 1px 10px rgba(255, 255, 255, 0.6);
  font-size: 20px;
  color: black;
  cursor: pointer;
}

aside {
  position: relative;
  width: 97.657vw;
  margin: 0 auto;
  margin-top: 170px;
}
aside .img-left {
  position: absolute;
  top: 0;
  left: 0;
  width: 23vw;
}
aside .img-right {
  position: absolute;
  top: 0;
  right: 0;
  width: 23vw;
}
aside h2 {
  text-align: center;
  padding-top: 40px;
  font-size: 42px;
}
aside .partners {
  width: 74.657vw;
  margin: 0 auto;
  text-align: center;
}
aside .partners .text-partners {
  margin-top: 100px;
  font-size: 30px;
  display: flex;
  justify-content: space-between;
}
aside .description {
  width: 54.657vw;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  margin-top: 100px;
}

.catalog {
  width: 73.646vw;
  margin: 0 auto;
  margin-top: 200px;
  text-align: center;
}
.catalog h2 {
  text-align: center;
  padding-top: 40px;
  font-size: 42px;
}
.catalog ul {
  list-style: none;
  display: flex;
  gap: 50px;
  margin-top: 100px;
}
.catalog ul li {
  font-size: 24px;
  text-decoration: none;
  margin: auto;
}
.catalog ul li span {
  text-decoration: none;
}

.connection {
  text-align: center;
  font-size: 24px;
  font-weight: 500;
  width: 30vw;
}
.connection button {
  margin-top: 60px;
  width: 18.302vw;
  height: 50px;
  border-radius: 50px;
  border-style: none;
  box-shadow: -1px -1px 10px rgba(255, 255, 255, 0.6), 1px 1px 10px rgba(255, 255, 255, 0.6);
  font-size: 20px;
  color: black;
  cursor: pointer;
}

.advantages {
  width: 99vw;
  background-image: url(./img/bg-.png);
  background-repeat: no-repeat;
  background-size: 100%;
  margin-top: 150px;
  position: relative;
}
.advantages .vector {
  width: 99vw;
}
.advantages h3 {
  font-size: 42px;
  text-align: center;
  position: relative;
  bottom: 150px;
}
.advantages .advanteges_container {
  width: 67vw;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  gap: 5vw;
}
.advantages .advanteges_container .img_container {
  background-color: rgba(24, 23, 21, 0.6);
  border-radius: 25px;
  padding: 22px 76px;
  width: 21.449vw;
}
.advantages .advanteges_container .img_container img {
  width: 10.981vw;
}
.advantages .advanteges_container .img_container p {
  text-align: center;
}

.pos-bot {
  position: relative;
  top: 80px;
}

.pos-top {
  position: relative;
  bottom: 100px;
}

h3 {
  text-align: center;
  font-size: 42px;
  margin-bottom: 77px;
}

.contact {
  width: 94.143vw;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}
.contact ul {
  list-style: none;
}
.contact ul li {
  font-size: 24px;
}

.yandex {
  width: 54.1vw;
  height: 350px;
}

footer {
  margin-top: 200px;
  padding-top: 70px;
  padding-bottom: 120px;
  background-color: black;
}
footer h1 {
  width: 95vw;
  margin: 0 auto;
  font-size: 42px;
}

.vector-bottom {
  position: relative;
  top: 15px;
}

@media (max-width: 1450px) {
  .advantages {
    background-size: 100% 100vw;
  }
  .advantages .advanteges_container {
    gap: 0;
    position: relative;
    bottom: 100px;
    width: 100vw;
  }
  .item_container .corner_right,
  .item_container .corner_left {
    width: 12.299vw;
  }
  .item_container .item {
    height: 270px;
  }
  .item_container .mane_photo {
    height: 200px;
  }
  .compani .section-container p {
    font-size: 20px;
  }
  .compani .company-img {
    height: 400px;
  }
  .compani h2 {
    width: 50vw;
    text-align: center;
  }
  main {
    padding-top: 50px;
  }
  main h2 {
    width: 40vw;
    font-size: 38px;
    line-height: 50px;
  }
  main p {
    width: 40vw;
    font-size: 22px;
    margin-top: 10px;
  }
  .vector-main {
    top: 125px;
  }
}
@media (max-width: 1150px) {
  .form_contact {
    width: 80vw !important;
    left: 10vw !important;
  }
  .form_contact ul {
    width: 26vw !important;
  }
  .form_contact input,
  .form_contact textarea,
  .form_contact h2,
  .form_contact .checkbox {
    width: 45vw !important;
  }
  .advantages .advanteges_container {
    gap: 0;
    position: relative;
    bottom: 100px;
  }
  .advantages .advanteges_container .img_container {
    width: 10vw;
  }
  .header_page ul li {
    font-size: 15px;
  }
  main {
    padding-top: 50px;
  }
  main h2 {
    width: 40vw;
    font-size: 32px;
    line-height: 35px;
    margin-bottom: 25px;
  }
  main p {
    width: 40vw;
    font-size: 22px;
    margin-top: 0;
  }
  .vector-main {
    top: 0px;
  }
}
@media (max-width: 1000px) {
  body {
    background-size: 100% 100vw;
  }
  body .vector-main {
    top: 10px;
  }
  .menu_img {
    width: 10vw;
    opacity: 1;
    visibility: visible;
  }
  .header_page nav ul {
    position: absolute;
    background-color: black;
    width: 100vw;
    height: 200px;
    opacity: 0;
    display: block;
    right: 13%;
    line-height: 40px;
    z-index: 2;
    text-align: center;
    visibility: hidden;
  }
}
@media (max-width: 800px) {
  .vector-main {
    top: 10px;
  }
  .contact ul li {
    font-size: 18px;
  }
  main button {
    font-size: 14px;
    margin-top: 15px;
  }
  main h2 {
    font-size: 25px;
    width: 50vw;
  }
  main p {
    font-size: 14px;
    width: 50vw;
  }
  .compani .company-img {
    height: 220px;
  }
  .compani button {
    font-size: 14px;
    margin-top: 15px;
  }
  .compani .section-container p {
    font-size: 14px;
    line-height: 20px;
  }
  aside img {
    width: 30vw;
  }
  aside .partners .text-partners {
    font-size: 20px;
  }
  .catalog ul {
    gap: 10px;
  }
  .catalog ul li {
    font-size: 16px;
  }
  .item_container {
    grid-template-columns: repeat(2, 40vw);
    gap: 10px;
  }
  .item_container .item {
    width: 40vw;
  }
  .item_container .item .mane_photo {
    width: 37vw;
  }
  .connection {
    width: 100vw;
  }
  .connection h4 {
    width: 70vw;
    margin: 0 auto;
  }
  .advantages .img_container {
    top: 0px;
  }
  .advantages .advanteges_container {
    left: 0%;
  }
  .advantages .advanteges_container .img_container {
    width: 15vw;
    font-size: 15px;
    padding: 10px 40px;
  }
}
@media (max-width: 500px) {
  .item_container {
    grid-template-rows: repeat(6, 200px);
  }
  .item_container .item {
    height: 150px;
  }
  .item_container .item .mane_photo {
    height: 130px;
  }
  .advantages .advanteges_container {
    display: block;
    position: relative;
    left: 0%;
  }
  .advantages .advanteges_container .img_container {
    left: 25%;
  }
  body {
    background-size: 100vw 130vw;
  }
  button {
    font-size: 14px !important;
    width: 50vw !important;
  }
  .catalog ul {
    display: block;
  }
}
.activeMenu {
  opacity: 1 !important;
  visibility: visible !important;
}

.form_contact {
  width: 50vw;
  height: 700px;
  position: fixed;
