  left: 30vw;
  top: 15%;
  visibility: hidden;
  display: flex;
}
.form_contact ul {
  background-image: url("./img/form_img.png");
  background-repeat: no-repeat;
  position: absolute;
  width: 15vw;
  height: 100%;
  padding-left: 10px;
  list-style: none;
}
.form_contact form {
  position: relative;
  left: 37%;
  z-index: 5;
  display: block;
  font-size: 34px;
  width: 27vw;
  text-align: center;
}
.form_contact form button {
  width: 18.302vw;
  height: 50px;
  border-radius: 50px;
  border-style: none;
