  color: black;
}
.form_contact form h2 {
  margin-bottom: 30px;
}
.form_contact form input {
  width: 27vw;
  height: 50px;
  background-color: rgba(0, 0, 0, 0);
  border: 4px white solid;
  margin-bottom: 30px;
  color: white;
  font-size: 20px;
}
.form_contact form ::-moz-placeholder {
  font-size: 20px;
  color: white;
  padding-left: 15px;
  height: 100%;
